@import url(https://fonts.googleapis.com/css2?family=Overpass:wght@100;400;700&display=swap);
* {
    margin: 0;
    padding: 0;
    font-family: 'Overpass', sans-serif;
    outline: 0;

}

html {
    scroll-behavior: smooth;
}

h1 {
    font-size: 3rem;
    text-align: center;
    padding: .5rem;
}
h2 {
    font-size: 2.5rem;
    text-align: center;
    color: #2d3436;
}

p{ 
    font-size: 2rem;
}

.hero {
    align-items: center;
    background: #2d3436;
    color: #fff;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    width: 100%;
}

.container {
    max-width: 1100px;
    margin: 0 auto;
}




.line {
    padding-top: 1rem;
    width: 80px;
    margin: 0 auto;
    border-bottom: 2px solid #28df99;
}

.sticky + .content {
    padding-top: 80px;
}

.content {
    padding-top: 16px;
}

section.about {
    padding-top: 4rem;
}

.about-text {
    font-size: 1.5rem;
    font-weight: 300;
    text-align: center;
    padding: 1rem;
}

#portafolio {
    padding: 80px 0;
    background-color: #f0f1f1;
    position: relative;
}

.flex-cards {
    display: flex;
    margin: 4rem;
}

.card {
    max-width: 500px;
}





.contact {
    padding: 80px 0;
    background-color: #2d3436;
    color: #fff;
}
.contact > h2 {
    color: #fff;
}

.contact .contact-form {
    padding-top: 4rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact .contact-form .input-form, .text-area {
    width: 100%;
    max-width: 350px;
    padding: 1rem;
    font-size: 1rem;
    background-color: #1e2122;
    border: 2px solid #1e2122;
    margin: .4rem;
    color: #fff;
}
.text-area {
    height: 120px;
}
.contact .contact-form .input-form:focus, .text-area:focus {
    border: 2px solid #28df99;
}

.loading {
    align-items: center;
    text-align: center;
    padding-top: 1rem;
}



@media (max-width: 377px) {
    h1 {
        font-size: 1.5rem;
    }
    p {
        font-size: 1rem;
    }
    .contact .contact-form .input-form, .text-area {
        max-width: 200px;
    }
}

@media (max-width: 768px) {
    .contact .contact-form .input-form, .text-area {
        max-width: 300px;
    }
}

@media (max-width: 950px) {
    .flex-cards {
        flex-direction: column;
        align-items: center;
        margin: 2rem;
    }

    .card {
        max-width: 800px !important;
    }
}


.footer {
    background-color: #1e2122;
    padding: 4rem 0;
}

.social {
    width: 100%;
    display: flex;
    justify-content: center;
}

.footer .copy {
    color: #585a5a;
    font-size: 1.2rem;
    padding-top: .5rem;
    text-align: center;
    font-weight: 300;
}
.footer .copy .year {
    color: #28df99;
}

.social .box {
    background-color: #2d3436;
    margin: 1rem;
    padding: 1rem;
    transition: all .2s ease-in;
}
.social .box:hover {
    cursor: pointer;
    background-color: #28df99;
}

.social .box .icon-media {
    color: #fff;
    cursor: pointer;
}




.navbar {
    border-bottom: 2px solid #28df99;
    background-color: #1e2122;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    z-index: 2;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
}


.navbar .nav-items {
    display: flex;
    justify-content: flex-end;
    transition: height .3s ease-in;
}


.nav-items .nav-item {
    list-style: none;
    padding: 1rem;
}

.nav-item .nav-link {
    text-decoration: none;
    color: #fff;
    transition: all .3s ease-in;
}
.nav-item .nav-link.active {
    color: #28df99;
    border-bottom: 2px solid #28df99;
}

.nav-item .nav-link:hover {
    color: #28df99;
}

.menu-icon {
    align-self: flex-end;
    display: none;
    padding: 1rem;
    cursor: pointer;
}

.nav-items.show {
    overflow: auto !important;
    height: 220px !important;
}

@media (max-width: 520px) {
    .menu-icon {
        display: block;
    }

    .navbar .nav-items {
        flex-direction: column;
        height: 0px;
        overflow: hidden;
    }
}
.card {
    max-width: 300px;
    padding: .5rem;
    border: 2px solid rgb(255, 255, 255);
    border-radius: 5px;
    margin: .5rem;
    box-shadow: 3px 6px 14px -4px rgba(0,0,0,0.56);
}

.card .card-header {
    text-align: center;
}

.card .card-header .card-icon {
    padding: .8rem 0;
}

.card .card-header .card-title {
    color: #2d3436;
    padding: .8rem 0;
    font-size: 1.2rem;
    font-weight: 700;
}

.card .card-body .card-text {
    text-align: center;
    font-size: 1rem;
    font-weight: 300;
    padding: .8rem 0;
}

.card .card-body .tools {
    text-align: center;
    padding: .8rem 0 0 0;
    color: #28df99;
}


.card .card-body .tools-items {
    text-align: center;
    font-size: 1rem;
    font-weight: 300;
    padding: .8rem 0;
}
button.button, a.button {
    background: transparent;
    border: 2px solid #fff;
    border-radius: 5px;
    color: #fff;
    padding: .5rem 1rem;
    margin-top: 1rem;
    font-size: 1.5rem;
    transition: all .5s ease-in;
    text-align: center;
}

a.button {
    background-color: #28df99;
    text-decoration: none;
}

button.button:hover, a.button:hover{
    background: #28df99;
    border: 2px solid #28df99;
    cursor: pointer;
}


/* @import url('@splidejs/splide/dist/css/themes/splide-default.min.css'); */

.modal {
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0, 0.4);
}

.modal-content {
    position: relative;
    background-color: #fff;
    border-radius: 10px;
    margin: auto;
    margin-bottom: 10rem;
    width: 60%;
    -webkit-animation: animateTop 0.6s;
            animation: animateTop 0.6s;
}
.modal-header {
    position: relative;
}
.modal-body {
    padding: 2rem;
}
.modal-body .info .modal-title {
    font-size: 2rem;
    color: rgb(73, 73, 73);
}
.modal-body .info .modal-desc {
    font-size: 1rem;
    padding-top: .5rem;
    padding-bottom: 1rem;
}


.close {
    padding: 1rem;
    color: black;
    position: absolute;
    right: 0%;
    font-size: 2rem;
    font-weight: bold;
    z-index: 100;

}
.close:hover, .close:focus {
    cursor: pointer;

}

li.splide__slide {
    text-align: center;
}
.img {
    width: 95%;
    height: auto;
    max-height: 100%;
    border-radius: 10px;
}

.external-links {
    display: flex;
}



@-webkit-keyframes animateTop {
    from { top: -300px; opacity: 0; }
    to { top: 0; opacity: 1; }
}



@keyframes animateTop {
    from { top: -300px; opacity: 0; }
    to { top: 0; opacity: 1; }
}

@media (max-width: 768px) {
    .splide__arrows   {
        display: none;
    }
    .modal-content {
        width: 80%;
    }
    .external-links {
        flex-direction: column;
    }
}

.proyects-grid {
    margin: 4rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
}

.proyect {
    box-shadow: 3px 6px 14px -4px rgba(0,0,0,0.56);
    border-radius: 10px;
    position: relative;
}

.proyect .img-box {
    overflow: hidden;
    border-radius: 10px;

}
.proyect .img-box img {
    width: 100%;
    transform: scale(1.1);
    transition: transform 1s;
}

.proyect .img-box img:hover  {
    transform: scale(1.3);
    
}

.proyect:hover >  .details {
    z-index: 1;
    opacity: 1;
}

.proyect .details {
    position: absolute;
    background-color:#1e5f74;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    top: 0;
    z-index: -1;
    transition: .7s;
    opacity: 0;
}

.proyect .details .details-content {
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.proyect .details .details-content .title-proyect {
    font-size: 2rem;
    color: #fff;
}
 .tecs-proyect {
    font-size: 1rem;
    color: #28df99;
}


@media (max-width: 950px) {
    .proyects-grid {
        grid-template-columns: repeat(2, 1fr);
        margin: 2rem;
    }
}

@media (max-width: 640px) {
    .proyects-grid {
        grid-template-columns: repeat(1, 1fr);
    }
}


